import React, { useEffect, useState } from "react";
import Axios from "axios";

export default function GoogleLogin(params) {
    const [code , setCode] = useState('')
    const handdleLogin = ()=>{
        let Code = document.querySelector('#code')
        let Survey = document.querySelector('#survey')
        let Result = document.querySelector('#result')
        Result.innerHTML = Survey.value+'&'+Code.value
        SignInGG(Code.value,Survey.value)
    }
    const SignInGG = async(code,url)=>{
        let data = {}
        data.code = code 
        data.url_hash = url
        Axios.post('https://w8pinnfqf3.execute-api.us-east-1.amazonaws.com/dev/api/v1/auth/google',data)
        .then(res=>{
            console.log(res);
        })
        .catch(err=>{
            console.log(err.response);
        })
        // let response2 = await wrapper.postData("/auth/google", data)
        // console.log(response2);
    }
    useEffect(()=>{
        let url = window.location.href
        url = url.split('&')
        setCode(url[1])
        // console.log(url[1]);
    },[])
    return(
        <div className="container">
            <h1>Inicio de sesion con google</h1>
            {/* <h3>{url[1]}</h3> */}
            <input type="text" placeholder="survey" id="survey"/>
            <input type="text" placeholder="code" id="code" value={code}/>
            <p id="result"></p>
            <button className="btn btn-success" onClick={()=>handdleLogin()}>Iniciar</button>
        </div>
    )
}
